import React, {useEffect, useState} from "react";
import {useParams} from "react-router-dom"
import {getItems} from "../../api/api";
import './ChildCategory.css'
import ProductCard from "../template/ProductCard";
import RouteTemplate from "../template/RouteTemplate";
import FilterComponent, {NavLeftMenu} from "./FilterComponent";
import Category from "./Category";
import LinkTemplate from "../template/LinkTemplate";
import NoProduct from "./NoProduct";
import LoadingCircle from "../template/LoadingCircle";
import BodyTemplate from "../template/BodyTemplate";
import HotProductNav from "../template/HotProduct/HotProductNav";
import CategorySkeleton from "../skeleton/CategorySkeleton";
import MetaTag from "../template/MetaTag/MetaTag";

const RightHCategory = ({state}) => {
    return (
        <React.Fragment>
            {
                <div className='child-category-right'>
                    <div className='child-category-title-container'>
                        <div className='child-category-title'>
                            Khuyến mại đặc biệt
                        </div>
                        <div style={{flexGrow:1}}></div>
                    </div>
                    {
                        state.isLoadingProduct ? <div className='child-category-loading-container'>
                                <div className='child-category-loading-wrapper'>
                                    <LoadingCircle/>
                                </div>
                            </div> :
                            state.listProduct.length === 0 ? <NoProduct/> :
                                <div className='child-category-element-container'>
                                    {
                                        state.listProduct.map((item, index) => {
                                            return (
                                                <div className='child-category-element' key={index}>
                                                    <ProductCard data={item}/>
                                                </div>
                                            )
                                        })
                                    }
                                </div>
                    }
                </div>
            }
        </React.Fragment>
    )
}
export default function HotCategory() {
    const [state, setState] = useState({
        isLoading: true,
        isLoadingProduct: true,
        listProduct: []

    })
    useEffect(() => {
        setState({...state, isLoadingProduct: true})
        getItems(`/all_product?page=1&item_per_page=16&order=is_hot,desc`, {timeout: 20000})
            .then(res => setState({
                ...state,
                listProduct: res.data.list,
                isLoading: false,
                isLoadingProduct: false,

            }))
            .catch(error => console.log(error))
    }, [])
    return (
        <BodyTemplate>
            <MetaTag title={'Sản phẩm HOT'} />
            <React.Fragment>
                {
                    state.isLoading ? <CategorySkeleton/> :
                        <Category
                            routeTemplate={<RouteTemplate data={[
                                {
                                    name: 'Trang chủ',
                                    path: '/'
                                },
                                {
                                    name: 'Sản phẩm mới'
                                }
                            ]}/>}
                            leftComponent={<div style={{marginRight: '1em', marginTop: '1em'}}>
                                    <NavLeftMenu type={0}/>
                                    <HotProductNav/>
                                </div>}
                            rightComponent={<RightHCategory state={state}/>}
                        />
                }
            </React.Fragment>
        </BodyTemplate>
    );
}
